import { React,Component } from "react";
import UserTags from "./user_tags";
class User extends Component{
    constructor(props){
        super(props);
        this.state = {
            userPrimaryTags:[],
            userSecondaryTags:[],
        }
    }   

    getQuestionTag(answer, question){
        const result = {primary:"", secondary:""}
        if(question.type === "simple")
            return result;
        else if(question.type === 'multi-choice') {

            const selectedChoiceindex = question.choices.indexOf(answer)
            if(selectedChoiceindex === -1)
                return result
            else
            {
                result.primary = question.primary_tag[selectedChoiceindex];
                result.secondary = question.secondary_tags[selectedChoiceindex];
                return result;
            }

        }
        else{
            alert("Unknown Question Type Detected");
            return result
        }
    }

    getFormTags(form_data){
        const answers = form_data.answers;
        const form_entries = form_data.form_blueprint
        const primary = [];
        const secondary = [];
        answers.forEach( (answer, index) => {
            const tags = this.getQuestionTag(answer, form_entries[index]);
            primary.push(tags.primary)
            secondary.push(tags.secondary)
        });

        return {primary:primary, secondary:secondary}
    }

    componentDidMount(){
        let userPrimaryTags = this.state.userPrimaryTags.slice();
        let userSecondaryTags = this.state.userSecondaryTags.slice();

        this.props.user_form_data.forEach( user_submission => {
            const tags = this.getFormTags(user_submission);
            if(tags.primary !== "")
                userPrimaryTags = userPrimaryTags.concat(tags.primary);
            if(tags.secondary !== "") 
                userSecondaryTags = userSecondaryTags.concat(tags.secondary);
        });

        this.setState({
            userPrimaryTags:userPrimaryTags,
            userSecondaryTags:userSecondaryTags
        })
    }

    render(){
        return(
            <div className='border p-6'>
                <h1>Username - {this.props.username}</h1>
                <h2>ID - {this.props.user_id}</h2>
                <div>
                    <UserTags primary_tag={this.state.userPrimaryTags} secondary_tags={this.state.userSecondaryTags}/>
                </div>
            </div>
        );
    }

}
export default User;