import { root } from "postcss";
import React from "react"
import AskWhich from "./ask_which.js";
import Node from "./node_element";
import Help from "./help";
const graphlib = require("graphlib");


class TreeEdit extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedQuestion: null,
            selectedNode: null,
            assignedQuestionIndexes: this.props.aqi,
            directConnectionMode:false,
            diFirst:null,
            graph:null,
            diList:[],
            root_index:this.props.root_index,
        }
        if(this.props.graph !== null)
        {
            this.state.graph = this.cloneGraph(this.props.graph);
            this.state.assignedQuestionIndexes = this.props.aqi.slice();
            this.state.diList = JSON.parse(JSON.stringify(this.props.diList))
        }
        this.setRoot = this.setRoot.bind(this);
        this.setNode = this.setNode.bind(this);
        this.reset = this.reset.bind(this);
        this.directConnection = this.directConnection.bind(this);
        this.toggleDi = this.toggleDi.bind(this);
    }

    setRoot(e, index) {
        const form = this.props.form_data[index];
        const assignedQuestionIndexes = this.state.assignedQuestionIndexes.slice();
        assignedQuestionIndexes.push(index);
        const graph = new graphlib.Graph({multigraph: true});
        graph.setNode(index,{type:"normal"})
        this.setState({
            graph:graph,
            root_index:index,
            assignedQuestionIndexes:assignedQuestionIndexes
        })
    }

    cloneGraph(g){
        return graphlib.json.read(graphlib.json.write(g))
    }


    directConnection(end){
        if(!this.state.diFirst)
            return;
        const graph = this.cloneGraph(this.state.graph)
        const diList = this.state.diList.slice();
        const [start, label] = this.state.diFirst;
        if(!graphlib.alg.isAcyclic(graph))
        {
            alert("That creates a cycle");
            return; 
        }
        graph.setNode(start)
        graph.setEdge(start,end,{type:'pointer'}, label);

        if(!graphlib.alg.isAcyclic(graph))
        {
            alert("That Creates A Cycle")
            return;
        }

        diList.push({"from":start,"to":end})
        
        this.setState({
            graph:graph,
            directConnectionMode:false,
            diFirst:null,
            diList:diList

        })
    }


    setNode(parent_node, choice) {

        if(this.state.directConnectionMode)
        {   
            this.setState({
                diFirst:[parent_node,choice]
            })
            return
        }

        if (this.state.selectedQuestion === null)
            return

        const graph = this.cloneGraph(this.state.graph);
        const index = parseInt(this.state.selectedQuestion);

        graph.setNode(index,{type:'normal'})
        graph.setEdge(parent_node,index,{edge_label:choice, edge_type:'normal'},choice)

        const assignedQuestionIndexes = this.state.assignedQuestionIndexes.slice();
        assignedQuestionIndexes.push(index);

        this.setState({
            graph: graph,
            assignedQuestionIndexes: assignedQuestionIndexes,
            selectedQuestion: null,
        })
    }

    reset() {
        this.setState({
            graph: null,
            selectedQuestion: null,
            selectedNode: null,
            directConnectionMode:false,
            assignedQuestionIndexes: [],
            diList:[],
        })
    }

    toggleDi(){
        const oldVal = this.state.directConnectionMode;
        this.setState({
            directConnectionMode:!oldVal,
            diFirst:null,
        })
    }
    

    render() {
        if (this.state.graph === null) {
            return <AskWhich goBack={this.props.goToForm}  form_data={this.props.form_data} clickHandler={this.setRoot} />
        }
        return (
            <div className="space-y-8 mb-8 max-w-6xl mx-auto py-8">

                <Help />

                <div className="flex flex-row ">
                    {
                        !this.state.directConnectionMode ? 
                        <div className="flex-grow-0 flex flex-col space-y-3 border-l-4 border-t-4 border-b-4 rounded-l-md border-red-700 p-6">
                            <h2 className='text-xl font-bold p-2 rounded-md max-w-max'> List of Question </h2>
                            {this.props.form_data.map((f, index) => {
                                if (this.state.assignedQuestionIndexes.includes(index))
                                    return (
                                        <h2 key={index} className="rounded-md p-3 trans  text-white  bg-gray-500 max-w-max ">
                                            {index + 1} {f.questionText}
                                        </h2>
                                    )
                                return (
                                    <h2
                                        id={index}
                                        className={`rounded-md transition ease-in-out p-3   max-w-max  ${String(index) === String(this.state.selectedQuestion) ? " text-white bg-red-700 border border-red-700" : "bg-white text-black border border-red-700 "}`}
                                        key={index}
                                        onClick={e => this.setState({ selectedQuestion: e.target.id })}>
                                        {index + 1}  {f.questionText}
                                    </h2>
                                );
                            })
                            }
                        </div>
                        :
                        ""
                    }

                    <div className='flex-grow overflow-hidden border-4 rounded-r-md border-red-700 '>

                        <div className='p-6'>
                            <h1 className='text-xl font-bold p-2  rounded-md max-w-max'>
                                Choice Tree
                            </h1>
                            <div className="p-8 overflow-scroll max-h-md">
                                <Node graph={this.state.graph} current_node={this.state.root_index} form_data={this.props.form_data} setNode={this.setNode} directConnection={this.directConnection}/>
                            </div>
                        </div>

                        <div className='flex flex-row space-x-3 p-3 border-t border-red-700'>
                            <button onClick={this.reset} className="px-5 py-3 border border-red-700 hover:bg-red-700 hover:text-white transition-colors ease-in-out  rounded-md  ">
                                Reset
                            </button>
                            <button onClick={(e) => this.props.graphCommit(e, this.state.graph, this.state.assignedQuestionIndexes, this.state.root_index, this.state.diList)} className="px-5 py-3 border border-red-700 hover:bg-red-700 hover:text-white transition-colors ease-in-out rounded-md ">
                                Commit Changes
                            </button>
                        </div>
                    </div>
                </div>

                <div className="rounded-md border-4 border-red-700 p-8">
                    <div className='flex flex-row space-x-2'>
                        <h1 className='text-xl font-bold align-middle'> Direct Connection List</h1>
                        <button onClick={this.toggleDi} className="p-3 border border-red-700 hover:bg-red-700 hover:text-white transition-colors ease-in-out  rounded-md  ">
                                { this.state.directConnectionMode ? "ON" : "OFF"}
                        </button>
                    </div>
                    <div>
                        <ul>
                            { this.state.diList.map( (di, i) => <li key={i}>From:{di.from} to:{di.to}</li>)}
                        </ul>
                    </div>
                </div>
                <div>
                    <button onClick={ this.props.goToForm } className=" px-5 py-3 bg-red-700 rounded-md font-bold text-white">From Edit Mode</button>
                </div>
            </div>
        )
    }
}


export default TreeEdit