import {React,Component} from "react"
import User from "./user";

class UserData extends Component{
    constructor(props){
        super(props)
        this.state = {
            data:null,
            isLoading:true,
        }

    }

    componentDidMount(){
        const base64 = require("base-64");
        const headers = new Headers();
        headers.append("Authorization","Basic "+base64.encode(this.props.token+":boo"))
        fetch(`/api/user_data`,{
            methods:"GET",
            headers:headers,       
        })
        .then( d => d.json())
        .then(data => {
            this.setState({
                data:data.data,
                isLoading:false
            })
        })
    }

    render(){
        if(this.state.isLoading)
            return <h1>Loading</h1>
        const UserComponents = this.state.data.map( u => <User key={u.user_id} {...u} />) 
        return(
            <div className='flex flex-col max-w-5xl mt-6 border border-red-700 p-6 mx-auto'>
                <div className="flex flex-col space-y-6">
                    {UserComponents}
                </div>
            </div>
        );

    }
}
export default UserData