import { Graph } from "graphlib";
function Pointer(props){
    const current_class= String(props.parent_node+props.choice+props.next_node);
    return(
        <div>
            <h1 class={`text-center ${current_class}`}> Direct Connection </h1>
            <LineTo from={current_class} to={String(props.next_node)} borderColor="black" />
        </div>

        
    );

}
export default function Node(props){
    if (props.current_node === null)
        return <h1 className='text-center max-w-max p-2 hover:bg-red-700 hover:text-white font-bold rounded-md mx-auto border border-red-700' onClick={() => props.setNode(props.parent_node,props.choice)} >+</h1>
    const current_label = props.graph.node(props.current_node);
    const current_form = props.form_data[props.current_node];
    const out_edges = props.graph.outEdges(props.current_node)
    
    const choice_node = {};
    out_edges.forEach( ({w,name}) => {
        choice_node[name] = w;        
    });

    return(
    <div className='node  max-w-max mx-auto hover'>
            <h1 className={`node-text border border-black p-2 rounded-md  max-w-max mx-auto ${props.current_node}`} onClick={() => props.directConnection(props.current_node)}> {current_form.questionText}</h1> 
            <div className={`border border-black h-6 w-1 bg-black mx-auto`}></div>
                { current_form.type === "multi-choice" && current_form.choices.length > 1 ?
                    <div className='node-edge-list flex flex-row flex-nowrap justify-evenly space-x-5  border-t-4 border-black'>
                        {
                            current_form.choices.map(
                                (choice, i) =>{
                                    const next_node = choice_node[i] !== undefined ? choice_node[i] : null;
                                    const is_pointer = props.graph.edge(props.current_node, next_node,i)?.type === "pointer";
   
                                    return(
                                        <div key={i} className=''> 
                                            <div>
                                                <div className={`border border-black h-6 w-1 bg-black mx-auto`}></div>
                                                <div className='text-center'>{choice}</div> 
                                                <div className="border border-black h-6 w-1 bg-black mx-auto"></div>
                                            </div>
                                            {
                                                is_pointer ? <Pointer parent_node={props.current_node} next_node={next_node} choice={i}/> :
                                                    <Node 
                                                        key={i} 
                                                        directConnection={props.directConnection}
                                                        graph={props.graph} 
                                                        choice={i} 
                                                        form_data={props.form_data}  
                                                        setNode={props.setNode} 
                                                        parent_node={props.current_node} 
                                                        current_node={ choice_node[i] !== undefined ? choice_node[i] : null} 
                                                    />
                                            }
                                        </div>
                                    );
                                })
                        }
                    </div>
                    :(
                    props.graph.edge(props.current_node,choice_node['$$##$$'],"$$##$$")?.type === "pointer"? <Pointer parent_node={props.current_node} choice={"$$##$$"} next_node={choice_node['$$##$$']}/> :
                        <Node  
                            graph={props.graph}
                            directConnection={props.directConnection}
                            parent_node={props.current_node} 
                            setNode={props.setNode}
                            form_data={props.form_data} 
                            current_node={ choice_node["$$##$$"] !== undefined ? choice_node["$$##$$"] : null}
                            choice={"$$##$$"} 
                        />
                    )
                }
    </div>
    );
}