import { React, Component } from "react";
import SimpleQuestion from "./components/simple_question";
import MultiChoice from "./components/multi_choice";
import TreeEdit from "./components/tree_edit_v2";

const SIMPLE = "simple"
const MULTI_CHOICE = "multi-choice"
const SIMPLE_TEMPLATE =  { type:SIMPLE, questionText:"Question Text" };
const MULTI_CHOICE_TEMPLATE = { type:MULTI_CHOICE, questionText:"Question Text", choices:[ "Choice 1", "Choice 2", "Choice 3"],primary_tag:["","",""],secondary_tags:["","",""], multiAnswer:false};

function OptionsWrapper(props){
    return(
        <div className="flex flex-col  rounded-md transform hover:scale-105 hover:shadow-lg ease-in-out transition-transform border border-black">
            <div className='flex flex-row justify-end rounded-md p-1'>
                <button onClick={props.onDelete} className=" font-bold text-red-700 outline-none">
                    <svg  className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"></path></svg>
                </button>
            </div>
            <div className='p-3 rounded-b-md '>
                {props.children}
            </div>
        </div>
    );
}


function SubmitSuccesful(){
    return(
        <div className='max-w-4xl mx-auto align-middle min-h-screen'>
            <h1 className='text-5xl font-bold text-center'>Form Submitted Succesfully</h1>
        </div>
    )
}

class DynamicFormCreator extends Component {
    constructor(props){
        super(props);
        this.state = {
            heading:"",
            description:"",
            formEntries:[
                {
                    type:"simple",
                    questionText:"Enter Question",
                },
            ],
            viewState:null,
            mode:"form_edit",
            graph:null,
            root_index:null,
            aqi:[],
            diList:[],
        }
        this.componetize = this.componetize.bind(this);
        this.addQuestion = this.addQuestion.bind(this);
        this.handleChildUpdate = this.handleChildUpdate.bind(this);
        this.deleteHandler = this.deleteHandler.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.graphCommit = this.graphCommit.bind(this);
        this.goToForm = this.goToForm.bind(this);
    }

    componetize(entry,index){
        let component = null;
        switch (entry.type) {
            case "simple":
                component = <SimpleQuestion childIndex={index} data={entry} onUpdate={this.handleChildUpdate}/> ; break;
            case "multi-choice":
                component = <MultiChoice childIndex={index} data={entry} onUpdate={this.handleChildUpdate} /> ; break;
            default:
                component = <p>Default Component {index}</p>; break;
        }
        return(
            <OptionsWrapper key={index} onDelete={() => this.deleteHandler(index)}>
                {component}
            </OptionsWrapper>
        )
    }

    deleteHandler(index){
        let formEntries = this.state.formEntries.slice()
        formEntries = formEntries.slice(0,index).concat(formEntries.slice(index+1))
        this.setState({formEntries:formEntries})
    }
    
    handleChildUpdate(childIndex, modifiedState){
        const formEntries = this.state.formEntries.slice();
        formEntries[childIndex] = {...modifiedState}
        this.setState({formEntries:formEntries});
    }

    addQuestion(question_type){
        const formEntries = this.state.formEntries.slice()

        switch(question_type){
            case SIMPLE: 
                formEntries.push({...SIMPLE_TEMPLATE}); 
                break;
            case MULTI_CHOICE:
                const newmc = JSON.parse(JSON.stringify(MULTI_CHOICE_TEMPLATE))
                formEntries.push(newmc);
            default: <p>HAHA i'm default</p>

        }
        this.setState({formEntries:formEntries})
    }

    onSubmit(){
        if(this.state.viewState === "submitting")
            return;
        else
            this.setState({
                viewState:"submitting"
        })
        const graphlib = require("graphlib");
        let graph_json = {}
        if (this.state.graph !== null)
            graph_json = graphlib.json.write(this.state.graph)
        let base64 = require('base-64')

        const data = { heading:this.state.heading,
                       description:this.state.description,
                       formEntries:this.state.formEntries,
                       tree:graph_json,
                       root_index:this.state.root_index,
                    }
        if(this.state.graph === null)
                    data.type="normal";
        else
                    data.type="tree";
        const token = this.props.token
        let headers = new Headers()

        headers.append('Authorization', `Basic ${base64.encode(token+":boo")}`)
        headers.append('Content-Type','application/json')
        
        fetch("/api/create",{
            method:"POST",
            headers:headers,
            body: JSON.stringify(data)
        })
        .then( d => d.json() )
        .then(d => {
            if (d.msg == "success")
            {
                this.setState({
                    viewState:"submitted"
                })
            }
            else
            {
                console.log("submit error")
            }
        })
    }

    graphCommit(e,graph,aqi, root_index, diList){
        this.setState({
            graph:graph,
            aqi:aqi,
            root_index:root_index,
            diList:diList,
        })
    }

    goToForm(){
        this.setState({
            mode:'form_edit'
        })
    }
    render(){
        if(this.state.viewState === "submitted")
            return <SubmitSuccesful/>
        const FormComponents = this.state.formEntries.map(this.componetize);
        return(
                <div >
                        { this.state.mode === 'form_edit' ?
                            <div className='max-w-4xl mx-auto  py-8  '>
                                <h1 className='text-4xl font-extrabold border-b pb-2 border-black text-center'>
                                    Form Creator
                                </h1>
                                <div className='flex flex-col space-y-8 py-8'>
                                    <div className='flex flex-col  '>
                                        <input className="text-3xl font-bold  py-1 border-black focus:outline-none" type='text'placeholder='Enter Heading Here' value={this.state.heading} name='heading' onChange={(e) => this.setState({[e.target.name]:e.target.value}) }/>
                                        <textarea className="text-md py-1 text-gray-400  max-h-min  focus:outline-none " placeholder="Enter form description Here" value={this.state.description} rows='1' name='description' onChange={(e) => this.setState({[e.target.name]:e.target.value})}/>
                                    </div>

                                    {FormComponents}

                                    <div className="2xl:fixed 2xl:top-52 2xl:left-6 flex 2xl:flex-col 2xl:space-y-3 2xl:space-x-0 space-x-3 flex-row">
                                            <button onClick={() => this.addQuestion(SIMPLE)} className="p-3 border text-red-700 font-bold border-red-700 border-dark rounded-md  focus:outline-none hover:bg-red-700 hover:text-white ease-in-out transition-colors">Add Simple Question</button>
                                            <button onClick={() => this.addQuestion(MULTI_CHOICE)} className="p-3 border text-red-700 border-red-700 font-bold border-dark rounded-md  focus:outline-none hover:bg-red-700 hover:text-white ease-in-out transition-colors">Add Multiple Choice Question</button>
                                    </div>

                                </div>
                                <div className='flex flex-row space-x-1'>
                                    <button onClick={ e => this.setState({ mode: "tree_edit"}) } className=" px-5 py-3 bg-red-700 rounded-md font-bold text-white">{ this.state.mode === "form_edit" ? "Tree Edit Mode" : "Form Edit Mode"}</button>
                                    <button onClick={this.onSubmit} className=" px-5 py-3 bg-red-700 rounded-md font-bold text-white">Submit</button>
                                </div>
                            </div>
                        :
                            <TreeEdit form_data={this.state.formEntries} graphCommit={this.graphCommit} graph={this.state.graph} root_index={this.state.root_index} aqi={this.state.aqi} diList={this.state.diList} goToForm={this.goToForm} />
                        }
                </div>
        );
    }
}

export default DynamicFormCreator;