
function Question(props){

    const choices = props.choices?.map( (choice,i) => <li key={i}>{choice}</li>)
return(
    <div className="px-6 py-3 rounded-md border border-red-700 transform hover:scale-105 transition ease-in-out" onClick={(e) => props.clickHandler(e, props.index)}>
            <h1 className='text-lg '> <span className=""> {props.index + 1} </span>   {props.questionText}</h1>
            <ul className=" text-sm pl-3">
                { choices }
            </ul>
    </div>
)
}
function AskWhich(props){
const questionComponents = props.form_data.map((q,i) => <Question key={i} {...q} index={i} clickHandler={props.clickHandler}/>)
return(
    <div className="fixed z-0 inset-0  w-full bg-black h-full opacity-90 ">
        <div className=" opacity-100 mt-32 max-w-max  align-middle  bg-white border border-red-700  mx-auto rounded-md ">
                <div className="p-6 bg-red-700 text-white">
                    <h1 className='text-2xl font-bold'> Pick a Question, So we can setup the root of your tree</h1>
                </div>
                <div className="p-6 space-y-7 overflow-y-scroll">
                    {questionComponents}
                </div>
                <div className='p-6'>
                    <button onClick={props.goBack} className="px-5 py-3 bg-red-700 rounded-md font-bold text-white">
                        Go Back
                    </button>
                </div>
        </div>
    </div>
)
}

export default AskWhich;