import React,{Component} from 'react';

function TagComponent(props){
    return(
        <div className="flex flex-row space-x-3">
            <input type='text' onChange={props.onChange} value={props.primary_tag} placeholder='Primary Tag' name='primary_tag'  className="px-5 py-2 border-2  outline-none transition-colors ease-in-out rounded-md focus:border-red-700" autoComplete={"off"}/>
            <input type='text' onChange={props.onChange} value={props.secondary_tag} placeholder='Secondary Tags'  name='secondary_tags' className="px-5 py-2 border-2  outline-none transition-colors ease-in-out rounded-md focus:border-red-700" autoComplete={"off"}    />
        </div>
    );
}
function Choice(props){
    return(
        <div className=' flex flex-row space-x-3'>
            <div className='flex flex-row border-2 group focus-within:border-red-700 transition-colors ease-in-out rounded-md'>
                <input type='text' value={props.choice} name='choice' onChange={props.onChange} className="w-full px-5 py-2 rounded-md outline-none flex-grow "/>
                <button onClick={props.choiceDelete} className="text-red-600 align-middle p-1 focus:outline-none">
                    <svg  className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"></path></svg>
                </button>
            </div>  
            <TagComponent primary_tag={props.primary_tag} secondary_tags={props.secondary_tag} onChange={props.onChange}/>
        </div>
    )
}
class MultiChoice extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...this.props.data
        }
        this.changeHandle = this.changeHandle.bind(this);
        this.addChoice = this.addChoice.bind(this);
        this.choiceDeleteHandler = this.choiceDeleteHandler.bind(this);
    }
    
    componentDidUpdate(prevProps,prevState){
        
        if(JSON.stringify(prevState) !== JSON.stringify(this.state))
        {
            this.props.onUpdate(this.props.childIndex, this.state);
        }
    }
    
    changeHandle(e, index){
        const newState = JSON.parse(JSON.stringify(this.state))

        if(index === -1)
            newState[e.target.name] = e.target.type === "checkbox" ? e.target.checked : e.target.value
        else
        {
            if(e.target.name === 'choice')
            {
                newState.choices[index] = e.target.value
            }
            else if(e.target.name === 'primary_tag')
            {
                newState.primary_tag[index] = e.target.value
            }
            else if(e.target.name === 'secondary_tags')
            {
                newState.secondary_tags[index] = e.target.value
            }
        }

        this.setState(newState);
    }

    addChoice(){
        const choices = this.state.choices.slice();
        const primary_tag = this.state.primary_tag.slice();
        const secondary_tags = this.state.secondary_tags.slice();

        primary_tag.push("");
        secondary_tags.push("");
        choices.push("New Choice");

        this.setState({choices:choices, primary_tag:primary_tag, secondary_tags:secondary_tags});
    }

    choiceDeleteHandler(e,index){
        const choices = this.state.choices.slice();
        const primary_tag = this.state.primary_tag.slice();
        const secondary_tags = this.state.secondary_tags.slice();

        if(choices.length===2)
            return
        const newChoiceList = choices.slice(0,index).concat(choices.slice(index+1))
        const newPrimaryList = primary_tag.slice(0,index).concat(primary_tag.slice(index+1))
        const newSecondaryList =  secondary_tags.slice(0,index).concat(secondary_tags.slice(index+1))
        this.setState({choices:newChoiceList, primary_tag:newPrimaryList,secondary_tags:newSecondaryList})
    }

    render(){
        return(
            <div className='space-y-3'>
                <input type='text' value={this.state.questionText} className="w-full px-5 py-3 border-2 rounded-md focus:border-red-700 transition-colors ease-in-out outline-none" name="questionText" placeholder='Question Text' onChange={(e)=> this.changeHandle(e, -1)}/>
                <div>
                    <input type='checkbox' className="p-2 mx-2" checked={this.state.multiAnswer} onChange={(e) => this.changeHandle(e,-1)} name='multiAnswer'/>
                    Allow Multiple Answers?
                </div>
                <div className='space-y-3'>
                    { this.state.choices.map( (choiceText, choiceIndex) => <Choice 
                                                                                key={choiceIndex} 
                                                                                choice={choiceText}
                                                                                primary_tag={this.state.primary_tag[choiceIndex]}
                                                                                secondary_tags={this.state.secondary_tags[choiceIndex]} 
                                                                                onChange={(e) => this.changeHandle(e,choiceIndex)} 
                                                                                choiceDelete={(e) => this.choiceDeleteHandler(e,choiceIndex)}
                                                                            />)}
                    <button onClick={this.addChoice} className="font-semibold px-4 py-2 border text-red-700 rounded-md border-red-700 transition-colors hover:bg-red-700 hover:text-white "> New Choice </button>
                </div>
            </div>
        );
    }

}

export default MultiChoice